<script lang="ts">
  import { createEventDispatcher } from "svelte";

  import { stores } from "module/store/SettingsStore";
  import { ModuleSetting } from "module/store/ModuleSettings";
  import type { SearchResultItem } from "./SearchControllers";
  import KeyboardKey from "app/ui/KeyboardKey.svelte";

  const dispatch = createEventDispatcher();

  let {
    results = [],
    selectedIndex = 0,
  }: { results: SearchResultItem[]; selectedIndex: number } = $props();

  let density = $derived(stores[ModuleSetting.SEARCH_DENSITY]);

  let resultList: HTMLElement;

  $effect(() => {
    resultList?.children[selectedIndex]?.scrollIntoView({ block: "nearest" });
  });

  function selected(item: number, shiftKey: boolean) {
    dispatch("callAction", { item, shiftKey });
  }
</script>

<ul class="quick-insert-result density-{$density}" bind:this={resultList}>
  {#each results as result, i (result.item.id)}
    <li
      class:search-selected={i === selectedIndex}
      class="no-icon-result"
      title={result.item.name}
    >
      <a on:click={(e) => selected(result.item, e.shiftKey)}>
        <span>{result.item.name}</span>
        <span class="secondary">
          {#if $density === "spacious" && result.item.description}
            <span class="sub">{result.item.description}</span>
          {/if}
          {#each result.item.keybinds as bind}
            <KeyboardKey>{bind}</KeyboardKey>
          {/each}
        </span>
      </a>
    </li>
  {/each}
</ul>

<style>
  .density-spacious a {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  .secondary {
    display: flex;
    flex-direction: row;
    justify-content: end;
    flex-shrink: 0;
    gap: 0.4em;
  }
</style>
