<script lang="ts">
  import { createEventDispatcher } from "svelte";
  import { mloc } from "module/utils";
  import { stores } from "module/store/SettingsStore";
  import { ModuleSetting } from "module/store/ModuleSettings";
  import type { SearchFilter } from "module/store/Filters";

  const dispatch = createEventDispatcher();

  let {
    quickEditable = true,
    filterEditorOpen = false,
    showHelp = true,
    filter,
    value = "",
  }: {
    quickEditable?: boolean;
    filterEditorOpen?: boolean;
    showHelp?: boolean;
    filter?: SearchFilter;
    value?: string;
  } = $props();

  let selectedFilterTag = $derived(filter?.tag);
  let quickEditEnabled = $derived(stores[ModuleSetting.QUICK_FILTER_EDIT]);

  function onKeydown(event: KeyboardEvent) {
    if (event.key === "Backspace" && !value) {
      dispatch("clearFilter");
    }
    if (event.key === "ArrowUp") {
      event.preventDefault();
      dispatch("navigateUp");
    }
    if (event.key === "ArrowDown") {
      event.preventDefault();
      dispatch("navigateDown");
    }
    if (event.key === "Tab") {
      event.preventDefault();
      dispatch("switchAction", { shiftKey: event.shiftKey });
    }
    if (event.key === "Enter") {
      event.preventDefault();
      dispatch("submitInput", { shiftKey: event.shiftKey });
    }
    if (event.key === "Escape") {
      event.preventDefault();
      event.stopPropagation();
      dispatch("close");
    }
  }

  function inputChanged() {
    dispatch("inputChanged", value);
  }
</script>

<div class="search-editable-input">
  {#if selectedFilterTag}
    <span
      class="search-tag"
      class:filter-opened={filterEditorOpen}
      title="@{selectedFilterTag} - {filter?.subTitle}"
    >
      @{selectedFilterTag}<!-- <i class="fas fa-times" /> -->
    </span>
  {/if}
  <input
    placeholder={showHelp ? mloc("SearchHelpHint") : ""}
    aria-haspopup="listbox"
    type="text"
    bind:value
    spellcheck="false"
    on:keydown={onKeydown}
    on:input={inputChanged}
    on:blur
    on:focus
  />
  {#if $quickEditEnabled && quickEditable}
    <i
      title={mloc("FilterPopupTitle")}
      class="fas fa-filter edit-filter"
      on:click={() => dispatch("openFilterEditor")}
    ></i>
  {/if}
</div>

<style>
  .search-editable-input {
    white-space: nowrap;
    height: var(--input-height);
    display: flex;
    align-items: center;
    flex-shrink: 0;
