    pointer-events: all;
  }

  input[type="text"] {
    padding: 0 0.2em;
    border: none;
    box-shadow: none;
    background: none;
    width: auto;
    flex-grow: 1;
    outline: none;
    font-size: inherit;
    height: 100%;
  }
  input[type="text"]:focus {
    outline: none;
  }
  .search-tag {
    position: relative;
    background: var(--qiFilterAccent);
